'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import {
    Shield,
    Bell,
    User,
    Search,
    Filter,
    Download,
    Trash2,
    CheckCircle2,
    AlertTriangle,
    AlertCircle,
    Clock,
    ExternalLink,
    ChevronLeft,
    ChevronRight
} from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { SettingsModal } from '@/components/settings-modal';
import type { VerdictType } from '@/types';

// Mock scan history data
interface ScanHistoryItem {
    id: string;
    url: string;
    verdict: VerdictType;
    riskScore: number;
    scannedAt: string;
    duration: number;
}

const mockHistory: ScanHistoryItem[] = [
    { id: '1', url: 'https://secure-bank-login.com/auth', verdict: 'PHISHING', riskScore: 92, scannedAt: '2026-01-08T20:30:00Z', duration: 1245 },
    { id: '2', url: 'https://google.com', verdict: 'SAFE', riskScore: 5, scannedAt: '2026-01-08T20:25:00Z', duration: 320 },
    { id: '3', url: 'https://paypal-secure.net/verify', verdict: 'PHISHING', riskScore: 88, scannedAt: '2026-01-08T20:20:00Z', duration: 1567 },
    { id: '4', url: 'https://suspicious-domain.xyz/login', verdict: 'SUSPICIOUS', riskScore: 65, scannedAt: '2026-01-08T20:15:00Z', duration: 890 },
    { id: '5', url: 'https://github.com', verdict: 'SAFE', riskScore: 3, scannedAt: '2026-01-08T20:10:00Z', duration: 245 },
    { id: '6', url: 'https://microsoft-account.com/reset', verdict: 'SUSPICIOUS', riskScore: 72, scannedAt: '2026-01-08T20:05:00Z', duration: 1120 },
    { id: '7', url: 'https://amazon.com', verdict: 'SAFE', riskScore: 4, scannedAt: '2026-01-08T20:00:00Z', duration: 198 },
    { id: '8', url: 'https://bit.ly/3xAbCdE', verdict: 'SUSPICIOUS', riskScore: 58, scannedAt: '2026-01-08T19:55:00Z', duration: 2340 },
];

export default function HistoryPage() {
    const [searchQuery, setSearchQuery] = useState('');
    const [filterVerdict, setFilterVerdict] = useState<VerdictType | 'ALL'>('ALL');
    const [history, setHistory] = useState<ScanHistoryItem[]>(mockHistory);
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 10;

    const filteredHistory = history.filter(item => {
        const matchesSearch = item.url.toLowerCase().includes(searchQuery.toLowerCase());
        const matchesVerdict = filterVerdict === 'ALL' || item.verdict === filterVerdict;
        return matchesSearch && matchesVerdict;
    });

    const totalPages = Math.ceil(filteredHistory.length / itemsPerPage);
    const paginatedHistory = filteredHistory.slice(
        (currentPage - 1) * itemsPerPage,
        currentPage * itemsPerPage
    );

    const getVerdictBadge = (verdict: VerdictType) => {
        const styles = {
            SAFE: 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400',
            SUSPICIOUS: 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/30 dark:text-yellow-400',
            PHISHING: 'bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-400',
        };
        return <Badge className={styles[verdict]}>{verdict}</Badge>;
    };

    const getVerdictIcon = (verdict: VerdictType) => {
        switch (verdict) {
            case 'SAFE': return <CheckCircle2 className="h-4 w-4 text-green-500" />;
            case 'SUSPICIOUS': return <AlertTriangle className="h-4 w-4 text-yellow-500" />;
            case 'PHISHING': return <AlertCircle className="h-4 w-4 text-red-500" />;
        }
    };

    const formatDate = (dateString: string) => {
        const date = new Date(dateString);
        return date.toLocaleString();
    };

    const stats = {
        total: history.length,
        safe: history.filter(h => h.verdict === 'SAFE').length,
        suspicious: history.filter(h => h.verdict === 'SUSPICIOUS').length,
        phishing: history.filter(h => h.verdict === 'PHISHING').length,
    };

    return (
        <div className="min-h-screen bg-gradient-to-b from-slate-50 to-slate-100 dark:from-slate-950 dark:to-slate-900">
            {/* Navigation */}
            <nav className="sticky top-0 z-50 bg-white/80 dark:bg-slate-900/80 backdrop-blur-md border-b border-slate-200 dark:border-slate-800">
                <div className="container mx-auto px-4">
                    <div className="flex h-16 items-center justify-between">
                        <div className="flex items-center gap-8">
                            <Link href="/" className="flex items-center gap-2">
                                <div className="h-9 w-9 rounded-lg bg-gradient-to-br from-indigo-500 to-purple-600 flex items-center justify-center">
                                    <Shield className="h-5 w-5 text-white" />
                                </div>
                                <span className="font-bold text-lg">PhishGuard</span>
                            </Link>
                            <div className="hidden md:flex items-center gap-6">
                                <Link href="/dashboard" className="text-sm font-medium text-slate-500 hover:text-slate-900 dark:hover:text-white">Dashboard</Link>
                                <Link href="/history" className="text-sm font-medium text-slate-900 dark:text-white">History</Link>
                                <Link href="/threat-map" className="text-sm font-medium text-slate-500 hover:text-slate-900 dark:hover:text-white">Threat Map</Link>
                                <Link href="/settings" className="text-sm font-medium text-slate-500 hover:text-slate-900 dark:hover:text-white">Settings</Link>
                            </div>
                        </div>
                        <div className="flex items-center gap-3">
                            <Button variant="ghost" size="icon">
                                <Bell className="h-5 w-5" />
                            </Button>
                            <SettingsModal />
                            <div className="h-9 w-9 rounded-full bg-gradient-to-br from-amber-400 to-orange-500 flex items-center justify-center">
                                <User className="h-5 w-5 text-white" />
                            </div>
                        </div>
                    </div>
                </div>
            </nav>

            {/* Header */}
            <div className="border-b border-slate-200 dark:border-slate-800 bg-white/50 dark:bg-slate-900/50">
                <div className="container mx-auto px-4 py-8">
                    <h1 className="text-3xl font-bold text-slate-900 dark:text-white mb-2">Scan History</h1>
                    <p className="text-slate-600 dark:text-slate-400">Review and manage your previous URL scans</p>
                </div>
            </div>

            {/* Stats */}
            <div className="container mx-auto px-4 py-6">
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
                    <div className="bg-white dark:bg-slate-800 rounded-xl p-4 border border-slate-200 dark:border-slate-700">
                        <p className="text-sm text-slate-500 mb-1">Total Scans</p>
                        <p className="text-2xl font-bold text-slate-900 dark:text-white">{stats.total}</p>
                    </div>
                    <div className="bg-white dark:bg-slate-800 rounded-xl p-4 border border-slate-200 dark:border-slate-700">
                        <p className="text-sm text-green-600 mb-1">Safe</p>
                        <p className="text-2xl font-bold text-green-600">{stats.safe}</p>
                    </div>
                    <div className="bg-white dark:bg-slate-800 rounded-xl p-4 border border-slate-200 dark:border-slate-700">
                        <p className="text-sm text-yellow-600 mb-1">Suspicious</p>
                        <p className="text-2xl font-bold text-yellow-600">{stats.suspicious}</p>
                    </div>
                    <div className="bg-white dark:bg-slate-800 rounded-xl p-4 border border-slate-200 dark:border-slate-700">
                        <p className="text-sm text-red-600 mb-1">Phishing</p>
                        <p className="text-2xl font-bold text-red-600">{stats.phishing}</p>
                    </div>
                </div>

                {/* Filters */}
                <div className="bg-white dark:bg-slate-800 rounded-xl p-4 border border-slate-200 dark:border-slate-700 mb-6">
                    <div className="flex flex-wrap items-center gap-4">
                        <div className="flex-1 min-w-64">
                            <div className="relative">
                                <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-slate-400" />
                                <input
                                    type="text"
                                    placeholder="Search URLs..."
                                    value={searchQuery}
                                    onChange={(e) => setSearchQuery(e.target.value)}
                                    className="w-full pl-10 pr-4 py-2 bg-slate-50 dark:bg-slate-900 border border-slate-200 dark:border-slate-700 rounded-lg outline-none focus:ring-2 focus:ring-indigo-500"
                                />
                            </div>
                        </div>
                        <div className="flex items-center gap-2">
                            <Filter className="h-4 w-4 text-slate-400" />
                            <select
                                value={filterVerdict}
                                onChange={(e) => setFilterVerdict(e.target.value as VerdictType | 'ALL')}
                                className="px-3 py-2 bg-slate-50 dark:bg-slate-900 border border-slate-200 dark:border-slate-700 rounded-lg outline-none focus:ring-2 focus:ring-indigo-500"
                            >
                                <option value="ALL">All Verdicts</option>
                                <option value="SAFE">Safe</option>
                                <option value="SUSPICIOUS">Suspicious</option>
                                <option value="PHISHING">Phishing</option>
                            </select>
                        </div>
                        <Button variant="outline" className="gap-2">
                            <Download className="h-4 w-4" />
                            Export
                        </Button>
                    </div>
                </div>

                {/* History Table */}
                <div className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                    <table className="w-full">
                        <thead>
                            <tr className="border-b border-slate-200 dark:border-slate-700 bg-slate-50 dark:bg-slate-900/50">
                                <th className="text-left px-4 py-3 text-sm font-medium text-slate-600 dark:text-slate-400">URL</th>
                                <th className="text-left px-4 py-3 text-sm font-medium text-slate-600 dark:text-slate-400">Verdict</th>
                                <th className="text-left px-4 py-3 text-sm font-medium text-slate-600 dark:text-slate-400">Risk Score</th>
                                <th className="text-left px-4 py-3 text-sm font-medium text-slate-600 dark:text-slate-400">Scanned</th>
                                <th className="text-left px-4 py-3 text-sm font-medium text-slate-600 dark:text-slate-400">Duration</th>
                                <th className="text-right px-4 py-3 text-sm font-medium text-slate-600 dark:text-slate-400">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {paginatedHistory.map((item) => (
                                <tr key={item.id} className="border-b border-slate-100 dark:border-slate-700/50 hover:bg-slate-50 dark:hover:bg-slate-700/20">
                                    <td className="px-4 py-3">
                                        <div className="flex items-center gap-2">
                                            {getVerdictIcon(item.verdict)}
                                            <span className="text-sm text-slate-900 dark:text-white truncate max-w-xs">
                                                {item.url}
                                            </span>
                                        </div>
                                    </td>
                                    <td className="px-4 py-3">
                                        {getVerdictBadge(item.verdict)}
                                    </td>
                                    <td className="px-4 py-3">
                                        <span className={`text-sm font-medium ${item.riskScore >= 85 ? 'text-red-600' :
                                                item.riskScore >= 55 ? 'text-yellow-600' : 'text-green-600'
                                            }`}>
                                            {item.riskScore}%
                                        </span>
                                    </td>
                                    <td className="px-4 py-3">
                                        <div className="flex items-center gap-1 text-sm text-slate-500">
                                            <Clock className="h-3 w-3" />
                                            {formatDate(item.scannedAt)}
                                        </div>
                                    </td>
                                    <td className="px-4 py-3">
                                        <span className="text-sm text-slate-500">{item.duration}ms</span>
                                    </td>
                                    <td className="px-4 py-3 text-right">
                                        <div className="flex items-center justify-end gap-2">
                                            <Button variant="ghost" size="icon" className="h-8 w-8">
                                                <ExternalLink className="h-4 w-4" />
                                            </Button>
                                            <Button variant="ghost" size="icon" className="h-8 w-8 text-red-500 hover:text-red-600">
                                                <Trash2 className="h-4 w-4" />
                                            </Button>
                                        </div>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>

                    {/* Pagination */}
                    {totalPages > 1 && (
                        <div className="flex items-center justify-between px-4 py-3 border-t border-slate-200 dark:border-slate-700">
                            <p className="text-sm text-slate-500">
                                Showing {((currentPage - 1) * itemsPerPage) + 1} to {Math.min(currentPage * itemsPerPage, filteredHistory.length)} of {filteredHistory.length}
                            </p>
                            <div className="flex items-center gap-2">
                                <Button
                                    variant="outline"
                                    size="sm"
                                    disabled={currentPage === 1}
                                    onClick={() => setCurrentPage(p => p - 1)}
                                >
                                    <ChevronLeft className="h-4 w-4" />
                                </Button>
                                <span className="text-sm text-slate-600 dark:text-slate-400">
                                    Page {currentPage} of {totalPages}
                                </span>
                                <Button
                                    variant="outline"
                                    size="sm"
                                    disabled={currentPage === totalPages}
                                    onClick={() => setCurrentPage(p => p + 1)}
                                >
                                    <ChevronRight className="h-4 w-4" />
                                </Button>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
